import "@babel/polyfill";
import barba from '@barba/core';
import gsap from 'gsap';
import barbaRouter from '@barba/router';
// анимированный текст на главной
import { initPresentationText } from './presentation-text';
// Ф-ции для анимаций
import { 
  addSideLinksListeners,
  addMenuListContentListeners,
  addMenuClickListener,
  showSidenavPointers,
  navDotsHandler,
} from './page-animations-functions';
// Ф-ции для хуков barba
import {
  onOnce,
  onPageReload,
  onHomeToPageLeave,
  onHomeToPageEnter,
  onPageToSectionLeave,
  onPageToSectionEnter,
  onSectionToProjectsLeave,
  onSectionToProjectsEnter,
  onPageToPageLeave,
  onPageToPageEnter,
  onSectionToSectionLeave,
  onSectionToSectionEnter,
  onProjectsToSectionLeave,
  onProjectsToSectionEnter
} from './barba-hooks-functions';

export default () => {
  const container = document.querySelector('.main');

  if (container) {
    container.classList.remove('hidden');
  }

  /* Навешиваем обработчики событий и проч. */
  addSideLinksListeners();
  initPresentationText();
  addMenuClickListener();
  addMenuListContentListeners();
  
  barba.use(barbaRouter, {
    routes: [{
      name: 'projects',
      path: '(/projects.html)',
    }],
  });

  barba.hooks.enter((data) => {
    // Следим за отображением точечной навигации
    navDotsHandler(data);
  });

  barba.hooks.afterEnter((data) => {
    onPageReload(data);
  });

  // Переходы между страницами
  barba.init({
    debug: true,
    transitions:
    [
      // Первая загрузка
      {
        once(data) {
          onOnce(this, data);
        },
      },
      // С главной на внутреннюю страницу
      {
        name: 'home to page',
        from: {
          namespace: ['home'],
        },
        to: {
          namespace: ['page', 'projects'],
        },
        leave(data) {
          // console.log('home to page - leave');
          onHomeToPageLeave(this, data);
        },
        enter(data) {
          // console.log('home to page - enter');
          onHomeToPageEnter(this, data);
        },
        afterEnter(data) {
          showSidenavPointers();
        }
      },
      // Со внутренней страницы на главную
      {
        name: 'page to home',
        from: {
          namespace: ['page', 'section', 'projects'],
        },
        to: {
          namespace: ['home'],
        },
        leave(data) {
          onPageToPageLeave(this, data);
        },
        enter(data) {
          onPageToPageEnter(this, data);
        },
      },
      // Со внутренней (page) на вложенную внутреннюю (section)
      {
        name: 'page to section',
        from: {
          namespace: ['page'],
        },
        to: {
          namespace: ['section'],
        },
        leave(data) {
          onPageToSectionLeave(this, data);
        },
        enter(data) {
          onPageToSectionEnter(this, data);
        },
      },
      // Со вложенной (section) на вложенную внутреннюю (section)
      {
        name: 'section to section',
        from: {
          namespace: ['section'],
        },
        to: {
          namespace: ['section'],
        },
        leave(data) {
          // console.log('section to section - leave');
          onSectionToSectionLeave(this, data);
        },
        enter(data) {
          // console.log('section to section - enter');
          onSectionToSectionEnter(this, data);
        },
      },
      // Со вложенной (section) на страницу проектов (projects)
      {
        name: 'section to projects',
        from: {
          namespace: ['section'],
        },
        to: {
          route: ['projects'],
        },

        leave(data) {
          onSectionToProjectsLeave(this, data);
        },

        enter(data) {
          onSectionToProjectsEnter(this, data);
        },
      },
      // Со внутренней на внутреннюю
      {
        name: 'page to page',
        from: {
          namespace: ['page', 'section'],
        },
        to: {
          namespace: ['page'],
        },

        leave(data) {
          onPageToPageLeave(this, data);
        },
        enter(data) {
          onPageToPageEnter(this, data);
        },
      },
      // Со страницы проектов (projects) на вложенную внутреннюю (section)
      // Это касается перехода со страницы списка проектов на страницу детальной инфо кейса
      {
        name: 'projects to section',
        from: {
          namespace: ['projects'],
        },
        to: {
          namespace: ['section'],
        },
        leave(data) {
          onProjectsToSectionLeave(this, data);
        },
        enter(data) {
          onProjectsToSectionEnter(this, data);
        },
      },
      // Со страницы проектов (projects) на внутреннюю страницу (page)
      {
        name: 'projects to page',
        from: {
          namespace: ['projects'],
        },
        to: {
          namespace: ['page'],
        },
        leave(data) {
          console.log('projects to page leave');
          onPageToPageLeave(this, data);
        },
        enter(data) {
          console.log('projects to page enter');
          onPageToPageEnter(this, data);
        },
      },
      // С внутренней страницы (page) на сртаницу проектов (projects)
      {
        name: 'page to projects',
        from: {
          namespace: ['page'],
        },
        to: {
          namespace: ['projects'],
        },
        leave(data) {
          console.log('page to projects leave');
          onPageToPageLeave(this, data);
        },
        enter(data) {
          console.log('page to projects enter');
          onPageToPageEnter(this, data);
        },
      },
    ],
  });
};